.profile__name {
    font-size: 42px;
    line-height: 56px;
    font-weight: 500;
  
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    transition: .5s ease-in-out;
  
    height: 56px;
    padding: 0;
    margin: 0;
    align-self: center;
  }
  
  @media screen and (max-width: 730px) {
    .profile__name {
      font-size: 27px;
      text-align: center;
    }
  }