.profile__work {
    font-size: 18px;
    font-weight: normal;
    line-height: 22px;
  
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    transition: .5s ease-in-out;
  
    padding: 0;
    margin: 0;
    grid-column: 1/3;
  }
  
  @media screen and (max-width: 730px) {
    .profile__work {
      font-size: 14px;
      line-height: 17px;
      text-align: center;
    }
  }